<script setup>
// This starter template is using Vue 3 <script setup> SFCs
// Check out https://v3.vuejs.org/api/sfc-script-setup.html#sfc-script-setup
import HelloWorld from "./components/HelloWorld.vue";

const color_change = (color) => {
    let color_name;
    if (color == "#2080f0") color_name = "blue";
    else color_name = "red";

    document.body?.setAttribute(
        "style",
        `background-color: var(--my-${color_name});animation: to_${color_name} 1s linear;`
    );
};
</script>

<template>
    <n-space vertical align="center">
        <img
            alt="Vue logo"
            src="./assets/logo.png"
            style="margin-left: -300px"
        />
        <HelloWorld @color_change="color_change" />
    </n-space>
</template>

<style>
@keyframes to_blue {
    0% {
        background-color: var(--my-red);
    }

    100% {
        background-color: var(--my-blue);
    }
}

@keyframes to_red {
    0% {
        background-color: var(--my-blue);
    }

    100% {
        background-color: var(--my-red);
    }
}

body {
    height: 720px;
    --my-blue: #2080f055;
    --my-red: #d0305055;
}
</style>
